import React, { Component } from 'react'

class Footer extends Component {
  render () {
    if (this.props.data) {
      var networks = this.props.data.social.map(function (network) {
        return (
          <li key={network.name}><a href={network.url}><i
            className={network.className}
                                                       />
          </a>
          </li>
        )
      })
    }

    return (
      <footer>
        <div className='row'>
          <div className='twelve columns'>
            <ul className='social-links'>
              {networks}
            </ul>

            <ul className='copyright'>
              <li><a title="Rohan's GitHub Page" href=''> Rohan's GitHub Page </a></li>
              <li>Special thanks to
                <a title='Tim Baker' href='https://github.com/tbakerx/react-resume-template'> Tim Baker </a>
                and
                <a title='Styleshout' href='http://www.styleshout.com/'> Styleshout </a>
              </li>
            </ul>

          </div>
          <div id='go-top'><a
            className='smoothscroll'
            title='Back to Top' href='#home'
                           ><i
                             className='icon-up-open'
                           >
                            </i>
          </a>
          </div>
        </div>
      </footer>
    )
  }
}

export default Footer
